<template>
  <main class="BoxCat-Main-Container">
    <MainHeader v-if="displayRouterView"></MainHeader>
    <main class="Main-Router-Content" v-if="displayRouterView">
      <router-view></router-view>
    </main>
    <MainFooter v-if="displayRouterView"></MainFooter>
  </main>
</template>

<script>
import MainHeader from "@/components/this/main/header";
import MainFooter from "@/components/this/main/footer";

export default {
  data: () => ({
    displayRouterView: false,
  }),
  watch: {
    $route() {
      this.checkRouter();
    },
  },
  methods: {
    checkRouter() {
      if (this.$route.name == "NotFound") {
        this.displayRouterView = false;
      } else {
        this.displayRouterView = true;
      }
    },
  },
  components: {
    MainHeader,
    MainFooter,
  },
};
</script>

<style>
.BoxCat-Main-Container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: rgba(255, 255, 255, 0.9);
}

.Main-Router-Content {
  flex: 1;
  margin: auto;
  width: 100%;
  max-width: 1920px;
}

.Main-Footer {
  flex: 0;
}
</style>
