<template>
  <header class="BoxCat-Main-Header">
    <div class="Header" ref="MainHeader">
      <div class="site-title">
        {{ title }}
      </div>
      <div class="spacer"></div>
      <div class="actions-link">
        <a href="javascript:;">首页</a>
        <a href="javascript:;">关于</a>
      </div>
    </div>

    <div class="placeholder" ref="Placeholder"></div>
  </header>
</template>

<script>
export default {
  name: "MainHeader",
  data: () => ({
    title: "",
  }),
  mounted() {
    this.init();
    document.addEventListener("scroll", this.scroll);
  },
  methods: {
    init() {
      this.title =
        process.env.VUE_APP_SITE_TITLE != undefined
          ? process.env.VUE_APP_SITE_TITLE
          : "怎么连个标题都没有";
    },

    scroll() {
      if (
        this.$refs.MainHeader == undefined ||
        this.$refs.MainHeader == null ||
        this.$refs.Placeholder == undefined ||
        this.$refs.Placeholder == null
      ) {
        return false;
      }

      let Header = document.querySelector(".BoxCat-Header");
      let scrollTop =
        document.documentElement.scrollTop ||
        window.pageYOffset ||
        document.body.scrollTop;
      let clientHeight = Header.clientHeight;

      if (clientHeight - scrollTop <= 0) {
        this.$refs.MainHeader.classList.add("fixed");
        this.$refs.Placeholder.style.paddingTop =
          this.$refs.MainHeader.clientHeight + "px";
      } else {
        this.$refs.MainHeader.classList.remove("fixed");
        this.$refs.Placeholder.style.paddingTop = "0px";
      }
    },
  },
};
</script>

<style scoped>
.BoxCat-Main-Header .Header {
  display: flex;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 0 5px 0 rgba(255, 255, 255, 0.55);
  cursor: default;
  user-select: none;
}

.BoxCat-Main-Header .Header.fixed {
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  position: fixed;
}

.BoxCat-Main-Header .site-title {
  font-size: 26px;
  padding-left: 15px;
}

.BoxCat-Main-Header .spacer {
  flex-grow: 1 !important;
}

.BoxCat-Main-Header .actions-link {
  transition: all 0.2s;
}

.BoxCat-Main-Header .actions-link a {
  font-size: 23px;
  color: #424242;
  padding: 20px 20px;
  display: inline-block;
  text-decoration: none;
  transition: all 0.2s;
}

.BoxCat-Main-Header .actions-link a:hover {
  color: rgba(66, 66, 66, 0.79);
  background-color: rgba(0, 0, 0, 0.1);
}

.BoxCat-Main-Header .placeholder {
  background-color: rgba(255, 255, 255, 0.9);
}
</style>
