<template>
  <header class="BoxCat-Header" ref="HeaderBox">
    <div class="header-title" ref="TitleBox">
      <div class="title">
        <span
          v-if="disFor === true || disFor === false"
          v-for="(item, i) in title"
          :class="animation[i] != undefined ? animation[i] : ''"
          >{{ item }}</span
        >
      </div>
      <div class="sub-title" ref="SubTitle">
        <span v-if="hiddenSubTitle === true">{{ subTitle }}</span>

        <vue-typer
          v-if="hiddenSubTitle === false"
          :text="subTitle"
          :repeat="0"
          :preTypeDelay="100"
          :typeDelay="180"
          @typed="addTextShadow"
        ></vue-typer>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  data: () => ({
    title: [],
    time: 0,
    animation: [],
    subTitle: "一群鸽子的猫窝 (╯▽╰ )",
    hiddenSubTitle: true,
    loaded: false,
    disFor: false,
  }),
  created() {
    this.init();
  },
  mounted() {
    document.addEventListener("scroll", this.scroll);
  },
  watch: {
    $route() {
      this.init();
    },
  },
  methods: {
    init() {
      if (this.loaded === true) {
        this.disFor = !this.disFor;
        this.animation = [];
        this.title = [];
        this.time = 0;
        this.hiddenSubTitle = true;
        this.$refs.SubTitle.classList.remove("display");
      }
      this.loaded = true;

      let title =
        this.$route.name == "NotFound"
          ? "Not Found"
          : process.env.VUE_APP_SITE_TITLE;
      this.title = title.split("");

      this.$nextTick(function() {
        this.setAnimation();
      });

      this.subTitle =
        this.$route.name == "NotFound"
          ? "你好像来到了一个不存在的地方 (っ °Д °;)っ"
          : "一群鸽子的猫窝 (╯▽╰ )";
    },

    setAnimation() {
      var _this = this;
      this.title.forEach((item, key) => {
        this.time = this.time + 300;
        setTimeout(() => {
          _this.$set(_this.animation, key, "stroke");
        }, this.time);

        if (this.title.length - 1 == key) {
          this.displaySubTitle();
        }
      });
    },

    displaySubTitle() {
      var _this = this;
      this.time = this.time + 100;
      setTimeout(() => {
        _this.$refs.SubTitle.classList.add("display");
        _this.hiddenSubTitle = false;
      }, this.time);
    },

    addTextShadow() {
      this.$refs.TitleBox.classList.add("text-shadow");
    },

    scroll() {
      if (this.$refs.HeaderBox == undefined) {
        return false;
      }

      let scrollTop =
        document.documentElement.scrollTop ||
        window.pageYOffset ||
        document.body.scrollTop;

      var opacity = 1 - scrollTop / window.innerHeight;

      this.$refs.HeaderBox.style.opacity = opacity;
    },
  },
};
</script>

<style>
.BoxCat-Header {
  height: 100vh;
  padding: 0px 15px;
}

.BoxCat-Header .header-title {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.BoxCat-Header .header-title {
  color: #fff;
  font-size: 90px;
  transition: all 0.3s;
}

.BoxCat-Header .header-title.text-shadow {
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.55), 0 0 5px rgba(0, 0, 0, 0.35),
    0 0 5px rgba(255, 255, 255, 0.55);
}

.BoxCat-Header .header-title .title span {
  opacity: 0;
  display: inline-block;
  transition: all 0.3s;
  transform: translate3d(20px, 20px, 10px) scale(0.85);
}

.BoxCat-Header .header-title .title span.stroke {
  opacity: 1;
  transform: translate3d(0px, 0px, 0px) scale(1);
}

.BoxCat-Header .header-title .sub-title {
  font-size: 50%;
  opacity: 0;
  transition: all 0.3s;
}

.BoxCat-Header .header-title .sub-title.display {
  opacity: 1;
}

.BoxCat-Header .header-title .sub-title .vue-typer .custom.char {
  color: #fff;
}

.BoxCat-Header .header-title .sub-title .vue-typer .custom.caret {
  width: 3px;
  background-color: rgba(240, 98, 146, 0.9);
}

.BoxCat-Header .header-title .sub-title .vue-typer .custom.char.selected {
  background-color: rgba(240, 98, 146, 0.45);
}

@media (max-width: 1024px) {
  .BoxCat-Header .header-title {
    font-size: 70px;
  }
}

@media (max-width: 850px) {
  .BoxCat-Header .header-title {
    font-size: 50px;
  }
}
</style>
