<template>
  <div class="background-container">
    <div
      class="background"
      ref="backgroundImage"
      :style="{ backgroundImage: backgroundImage }"
    ></div>
  </div>
</template>

<script>
export default {
  name: "BoxCatBG",
  data: () => ({
    backgroundImage: "url('" + require("@/assets/75778903.jpg") + "')",
  }),
  mounted() {
    document.addEventListener("mousemove", this.mousemove);
    document.addEventListener("mouseleave", this.mouseleave);
  },
  methods: {
    mousemove($event) {
      var x = window.innerWidth / 2 - $event.clientX,
        y = window.innerHeight / 2 - $event.clientY;

      this.$refs.backgroundImage.style.background =
        this.backgroundImage +
        " calc(50% + " +
        x / 50 +
        "px) calc(50% + " +
        y / 50 +
        "px) no-repeat";
    },

    mouseleave($event) {
      //console.log($event);
      /*this.$refs.backgroundImage.style.background =
        this.backgroundImage + " 50% 50% no-repeat";*/
    },
  },
};
</script>

<style scoped>
.background-container {
  z-index: -1;
  width: 100vw;
  height: 100vh;
  position: fixed;
}

.background-container .background {
  width: 105%;
  height: 105%;
  margin: -1% -2%;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover !important;
}

.background-container .background::after {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  content: "";
  position: fixed;
  background-color: rgba(0, 0, 0, 0.35);
}
</style>
